<template>
  <article>
    <div
      v-for="(alert,index) in alertArrayProp"
      :key="index"
      class="alert alert-dismissible fade show d-flex align-items-center alert_item px-4"
      :class="alert.isDanger?'alert-danger':'alert-success'"
      role="alert"
    >
      <span>{{alert.text}}</span>
      <button class="btn btn-success ml-auto py-1 px-2" @click.prevent="deleteAlert(index)">
        <span>&times;</span>
      </button>
    </div>
  </article>
</template>
<script>
export default {
    props:['alertArrayProp'],
    methods:{
        deleteAlert(index){
            this.$emit('on-delete-alert',index)
        }
    }
};
</script>
<style lang="scss" scoped>
.alert_item {
  width: 200px;
}
</style>
