<template>
  <article class="d-flex container position-relative">
    <Alert
      :alertArrayProp="alertArray"
      v-on:on-delete-alert="deleteAlert"
      class="alert_section position-absolute"
    />
    <Aside class="col-2"/>
    <router-view class="col-10" v-on:push-alert="alert"></router-view>
  </article>
</template>
<script>
import Aside from "./components/admin_aside.vue";
import Alert from "./components/Alert.vue";
import AxiosClass from "@/config/axiosClass.js";
export default {
  data() {
    return {
      alertArray: []
    };
  },
  components: {
    Aside,
    Alert
  },
  name: "admin",
  methods: {
    deleteAlert(index) {
      this.alertArray.splice(index, 1);
    },
    alert(text, isDanger = false) {
      this.alertArray.push({ text: text, isDanger: isDanger });
      setTimeout(() => {
        this.alertArray.splice(0, 1);
      }, 1000);
    },
    ensureLogin() {
      if (this.$store.state.user_email !== "admin@gmail.com") {
        this.$router.push("/login");
      }
    }
  },
  beforeUpdate() {
    //this.ensureLogin();
  },
  created() {
    //this.ensureLogin();
  }
};
</script>
    
<style lang="scss" scoped>
.alert_section {
  right: 0;
  z-index: 10000;
}
.alert_item {
  width: 200px;
}
</style>
